@use 'bourbon/core/bourbon';
@use '@rm-frontend/base/source/0-base/1-tools/scss/helpers';
@use '@rm-frontend/base/source/0-base/1-tools/scss/breakpoint';

@mixin visual-image {
  /* critical:start */
  display: block;
  /* critical:end */

  @include breakpoint.media('≥s') {
    /* critical:start */
    @include bourbon.position(absolute, 0);

    .visual--no-crop & {
      position: static;
    }
    /* critical:end */
  }

  &--inbox {
    /* critical:start */
    @include bourbon.position(absolute, 0);
    /* critical:end */
  }

  &--indent {
    &-left {
      img {
        @include breakpoint.media('<m') {
          /* critical:start */
          object-position: 20% 50%;
          /* critical:end */
        }
      }
    }

    &-right {
      img {
        @include breakpoint.media('<m') {
          /* critical:start */
          object-position: 70% 50%;
          /* critical:end */
        }
      }
    }
  }

  &,
  * {
    /* critical:start */
    min-height: inherit;
    /* critical:end */
  }

  img {
    /* critical:start */
    object-fit: cover;
    width: 100%;
    height: 100%;
    /* critical:end */
  }

  @include helpers.no-print;
}
